import { useEffect, useState } from 'react';
import { useFriendContext } from '../../../contexts/FriendContext';
import { useFriend } from '../../../services/friendService';
import { useChat } from '../../../services/chatService';
import '../../../styles/FriendList.css';

function FriendList() {
    const [searchTerm, setSearchTerm] = useState('');
    const { friends, setFriends, isLoading } = useFriendContext();
    const { 
        handleRemoveFriend,
    } = useFriend();
    const { 
        handleChatClick
    } = useChat();

    const removeFriend = async (userId, type) => {
        // e.stopPropagation(); //   

        const response = await handleRemoveFriend(userId, type);
        if(response){
            setFriends(friends.filter(friend => friend.id !== userId));
        }
    };

    const filteredFriends = friends.filter(friend =>
        friend.name.toLowerCase().includes(searchTerm.toLowerCase())
    );

    if (isLoading) {
        return (
            <div className="loading-container">
                <div className="loading-spinner"></div>
                <p>   ...</p>
            </div>
        );
    }

    return (
        <div className="friend-list-container">
            <div className="friend-list-header">
                <h2> </h2>
                <div className="friend-list-search">
                    <input
                        type="text"
                        placeholder=" ..."
                        value={searchTerm}
                        onChange={(e) => setSearchTerm(e.target.value)}
                    />
                </div>
            </div>

            {filteredFriends.length > 0 ? (
                <div className="friends-grid">
                    {filteredFriends.map((friend) => (
                        <div key={friend.id} className="friend-card">
                            <button 
                                className="remove-friend-button"
                                onClick={(e) => removeFriend(friend.id, 'DELETE')}
                            >
                            </button>
                            <div className="friend-avatar">
                                <div className="friend-default-avatar">
                                    {friend.name.charAt(0).toUpperCase()}
                                </div>
                            </div>
                            <div className="friend-info">
                                <h3>{friend.name}</h3>
                            </div>
                            <button
                                className="chat-button"
                                onClick={() => handleChatClick(friend.id)}
                            >
                                
                            </button>
                        </div>
                    ))}
                </div>
            ) : (
                <div className="no-friends">
                    <div className="no-friends-icon"></div>
                    <h3> </h3>
                    <p>  !</p>
                </div>
            )}
        </div>
    );
}

export default FriendList; 