import { useEffect, useState } from 'react';
import { useChatContext } from '../../contexts/ChatContext';
import { useAuth } from '../../contexts/AuthContext';
import { useNavigate } from 'react-router-dom';
import { useChat } from '../../services/chatService';
import '../../styles/ChatList.css';
import CreateGroupChatModal from '../../components/chat/CreateGroupChat/CreateGroupChatModal';

function ChatListPage() {
    const [showCreateModal, setShowCreateModal] = useState(false);
    const { chatList, C_isLoading } = useChatContext();
    const { isAuthenticated } = useAuth();
    const navigate = useNavigate();
    const {
        formatTime,
        createGroup,
    } = useChat();

    // console.log(chatList);
    const handleCreateGroup = (groupData) => {
        createGroup(groupData.name, groupData.participants);
    };

    const handleChatClick = (roomId) => {
        sessionStorage.setItem('room_id', roomId);
        navigate('/chat');
    };

    if (C_isLoading) {
        return (
            <div className="loading-container">
                <div className="loading-spinner"></div>
                <p>   ...</p>
            </div>
        );
    }

    return (
        <div className="chat-list-container">
            <div className="chat-list-header">
                <h2></h2>
                <div>
                    <button 
                    className="create-group-btn"
                    onClick={() => setShowCreateModal(true)}
                    >
                         
                    </button>

                    <CreateGroupChatModal
                        show={showCreateModal}
                        onHide={() => setShowCreateModal(false)}
                        onCreate={handleCreateGroup}
                    />
                </div>
            </div>
            
            {chatList.length > 0 ? (
                <div className="chat-rooms">
                    {chatList.map((chat) => (
                        <div 
                            key={chat.room_id} 
                            className="chat-room-card"
                            onClick={() => handleChatClick(chat.room_id)}
                        >
                            <div className="chat-room-info">
                                <div className="chat-room-avatar">
                                    <div className="chat-default-avatar">
                                        {/* {chat.name ? chat.name.charAt(0).toUpperCase() : '?'} */}
                                    </div>
                                </div>
                                <div className="chat-room-details">
                                    <div className="chat-room-header">
                                        <h3>{chat.room_name ? chat.room_name : chat.name}</h3>
                                        <span className="chat-time">{formatTime(chat.last_message_time)}</span>
                                    </div>
                                    <div className="chat-room-footer">
                                        <p className="last-message">
                                            {chat.last_message || '  !'}
                                        </p>
                                        {chat.unread_count > 0 && (
                                            <span className="unread-badge">{chat.unread_count}</span>
                                        )}
                                    </div>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            ) : (
                <div className="no-chats">
                    <div className="no-chats-icon"></div>
                    <h3>  </h3>
                    <p>   !</p>
                    <button 
                        className="start-chat-button"
                        onClick={() => navigate('/friends')}
                    >
                          
                    </button>
                </div>
            )}
        </div>
    );
}

export default ChatListPage; 