 import { useEffect } from 'react';
 import '../../../styles/modal.css';
 import { useFriend } from '../../../services/friendService';
 import { useFriendContext } from '../../../contexts/FriendContext';
 
 // AddFriendModal.jsx
 const AddFriendModal = ({ onClose }) => {
     const { 
         searchResults,
         isSearching,
         activeTab,
         setActiveTab,
         handleSearch,
         handleAddFriend,
         handleRemoveFriend,
         handleAcceptRequest,
         handleRejectRequest,
         setSearchResults,
     } = useFriend();
    const { friendRequests } = useFriendContext();
 
     const handleSubmit = (e) => {
     e.preventDefault();
     };
 
     const handleNameClick = async (e) => {
         const value = document.getElementById('search_val').value;
 
         if (value.length > 0) {
             handleSearch(value);
         }
     };
 
     const removeFriend = async (userId, type) => {
 
         const response = await handleRemoveFriend(userId, type);
         if(response){
             setSearchResults(prev => 
                 prev.map(user => 
                     user.id === userId 
                         ? {...user, status: null} 
                         : user
                 )
             );
         }
     }
 
     return (
     <div className="modal-overlay">
         <div className="modal-content">
             <div className="tab_wrap">
                 <div className={activeTab === 'tab1' ? 'tab_btn active' : 'tab_btn'} onClick={() => setActiveTab('tab1')}></div>
                 <div className={activeTab === 'tab2' ? 'tab_btn active' : 'tab_btn'} onClick={() => setActiveTab('tab2')} ></div>
             </div>
             <div id="cont_wrap">
                 <div className="cont_add" style={{ display: activeTab === 'tab1' ? 'block' : 'none'}}>
                     {friendRequests.length > 0 ? (
                         <div className="friend-requests">
                             {friendRequests.map(request => (
                                 <div key={request.id} className="request-item">
                                     <span>{request.name}</span>
                                     <div className="request-actions">
                                         <button onClick={() => handleAcceptRequest(request.id)}></button>
                                         <button onClick={() => handleRejectRequest(request.id)}></button>
                                     </div>
                                 </div>
                             ))}
                         </div>
                     ) : (
                         <div className="no-requests">   .</div>
                     )}
                 </div>
                 <div className="cont_search" style={{ display : activeTab === 'tab2' ? 'block' : 'none'}}>
                     <form onSubmit={handleSubmit}>          
                         {searchResults.length > 0 ? (
                             <div className="search-results">
                                 {searchResults.map(user => (
                                     <div key={user.id} className="user-item">
                                         <span>{user.name}</span>
                                         {user.status == null ? (
                                             <button 
                                                 onClick={(e) => handleAddFriend(user.id)}
                                                 className="add-friend-btn"
                                             >
                                                  
                                             </button>
                                         ) : (
                                             <button onClick={(e) => removeFriend(user.id, user.status === 'accepted' ? 'DELETE' : 'CANCEL')} className="requested-btn">
                                                 {user.status === 'accepted' ? '' : ''}
                                             </button>
                                         )}
                                     </div>
                                 ))}
                             </div>
                         ) : (
                             <div className="search-results no-requests">
                                 <p>     .</p>
                             </div>
                         )}
                         <input
                             id="search_val"
                             type="text"
                             placeholder="  "
                         />
                         
                         {/*    */}
                         {isSearching && <div className="searching"> ...</div>}
                         
                         <button type="button" onClick={handleNameClick}></button>
                     </form>
                 </div>
             </div>
             
             <button onClick={onClose}></button>
         </div>
     </div>
     );
 };
 
 export default AddFriendModal; 