import Echo from 'laravel-echo';
import Pusher from 'pusher-js';

const pusherConfig = {
    broadcaster: 'pusher',
    key: process.env.REACT_APP_PUSHER_APP_KEY,
    cluster: process.env.REACT_APP_PUSHER_APP_CLUSTER,
    forceTLS: false,
    enabledTransports: ['ws', 'wss'],
    authEndpoint: window.location.origin + '/broadcasting/auth',
    auth: {
        headers: {
            'X-CSRF-TOKEN': document.querySelector('meta[name="csrf-token"]')?.getAttribute('content') || ''
        },
        credentials: 'include' //  
    }
};

const pusher = {
    initialize: () => {
        if (!process.env.REACT_APP_PUSHER_APP_KEY) {
            console.error('Pusher    .');
            return null;
        }
        return new Echo(pusherConfig);
    },

    getInstance: () => {
        if (!window.Echo) {
            window.Echo = pusher.initialize();
        }
        return window.Echo;
    },

    subscribe: (channelName, eventName, callback) => {
        const echo = pusher.getInstance();
        if (!echo) return;
        
        echo.channel(channelName).listen(`.${eventName}`, callback);
    },

    unsubscribe: (channelName) => {
        const echo = pusher.getInstance();
        if (!echo) return;
        
        echo.leave(channelName);
    },

    subscribePresence: (channelName, handlers) => {
        const echo = pusher.getInstance();
        if (!echo) return;

        try {
            const channel = echo.join(channelName);
            
            if (handlers.here) {
                channel.here(handlers.here);
            }
            if (handlers.joining) {
                channel.joining(handlers.joining);
            }
            if (handlers.leaving) {
                channel.leaving(handlers.leaving);
            }

            return channel;
        } catch (error) {
            console.error('Presence channel subscription error:', error);
            return null;
        }
    }
};

export default pusher;