import { useState } from 'react';
import { Link, useLocation } from 'react-router-dom';
import { useAuth } from '../../../contexts/AuthContext';
import AddFriendModal from '../../friend/AddFriendModal/AddFriendModal';
import { useFriendContext } from '../../../contexts/FriendContext';
import './Header.css';

const Header = () => {
    const { user, logout } = useAuth();
    const [isModalOpen, setIsModalOpen] = useState(false);
    const { friendRequestCount } = useFriendContext();
    const location = useLocation();

    return (
        <header className="header">
            <nav className="nav">
                <div className="navLeft">
                    <Link to="/friends" className={`navLink ${location.pathname === '/friends' ? 'activeLink' : ''}`}>
                        
                    </Link>
                    <Link to="/chatList" className={`navLink ${location.pathname === '/chatList' ? 'activeLink' : ''}`}>
                        
                    </Link>
                </div>

                <div className="navRight">
                    <div style={{ position: 'relative' }}>
                        <button 
                            onClick={() => setIsModalOpen(true)}
                            className="addFriendButton"
                        >
                            <svg 
                                width="20" 
                                height="20" 
                                viewBox="0 0 24 24" 
                                fill="none" 
                                stroke="currentColor" 
                                strokeWidth="2"
                            >
                                <path d="M16 21v-2a4 4 0 0 0-4-4H5a4 4 0 0 0-4 4v2" />
                                <circle cx="8.5" cy="7" r="4" />
                                <line x1="20" y1="8" x2="20" y2="14" />
                                <line x1="23" y1="11" x2="17" y2="11" />
                            </svg>
                            {friendRequestCount > 0 && (
                                <span className="requestCountStyle">
                                    {friendRequestCount}
                                </span>
                            )}
                        </button>
                    </div>
                    {isModalOpen && (
                        <AddFriendModal 
                            isOpen={isModalOpen}
                            onClose={() => setIsModalOpen(false)}
                        />
                    )}
                    {user && (
                        <span className="userInfo">
                            {user.name} 
                        </span>
                    )}
                    <button 
                        onClick={() => logout()}
                        className="logoutButton"
                    >
                        Logout
                    </button>
                </div>
            </nav>
        </header>
    );
};

export default Header;