import React, { createContext, useContext, useEffect, useState } from 'react';
import { useAuth } from './AuthContext';
import { chatApi } from '../api/chatApi';
import pusher from '../api/pusher';

const ChatContext = createContext();

export const ChatProvider = ({ children }) => {
    const { isAuthenticated, user } = useAuth();
    const [chatList, setChatList] = useState([]);
    const [C_isLoading, C_setIsLoading] = useState(true);

    const fetchChatList = async () => {
        try {
            const response = await chatApi.chatList();
            setChatList(response.data);
            C_setIsLoading(false);
        } catch (error) {
            console.error('  :', error);
            C_setIsLoading(false);
        }
    };

    useEffect(() => {
        if (isAuthenticated) {
            fetchChatList();

            pusher.subscribe(`chat.${user.id}`, 'MessageSent', (e) => {
                const room_id = sessionStorage.getItem('room_id') || '';
                setChatList(prev => {
                    const index = prev.findIndex(c => Number(c.room_id) === Number(e.message.room_id));
                    if (index === -1) return prev;

                    const isTop = index === 0;
                    const isCurrentRoom = Number(e.message.room_id) === Number(room_id);

                    const updated = {
                        ...prev[index],
                        last_message: e.message.content,
                        last_message_time: e.message.created_at,
                        unread_count: isCurrentRoom ? prev[index].unread_count : (prev[index].unread_count || 0) + 1
                    };

                    if (isTop) {
                        const copy = [...prev];
                        copy[0] = updated;
                        return copy;
                    }

                    return [updated, ...prev.slice(0, index), ...prev.slice(index + 1)];
                });
            });

            pusher.subscribe(`create_room.${user.id}`, 'CreateRoomEvent', (e) => {
                // console.log(e);
                setChatList(prevList => [e.roomInfo, ...prevList]);
            });

            return () => {
                pusher.unsubscribe(`chat.${user.id}`);
                pusher.unsubscribe(`create_room.${user.id}`);
            };

        }
    }, [isAuthenticated]);


    return (
        <ChatContext.Provider value={{ 
            chatList,
            C_isLoading,
            setChatList
        }}>
            {children}
        </ChatContext.Provider>
    );
};

export const useChatContext = () => {
    const context = useContext(ChatContext);
    if (!context) {
        throw new Error('useChatContext must be used within a FriendProvider');
    }
    return context;
};