import { chatApi } from '../api/chatApi';
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useChatContext } from '../contexts/ChatContext';
import { useAuth } from '../contexts/AuthContext';

export const useChat = () => {
    const [messages, setMessages] = useState([]);
    const [roomInfo, setRoomInfo] = useState([]);
    const [isLoading, setIsLoading] = useState(true);
    const room_id = sessionStorage.getItem('room_id');
    const [participants, setParticipants] = useState([]);
    const navigate = useNavigate();
    const { setChatList } = useChatContext();
    const { user } = useAuth();

    //  
    const handleSendMessage = async (message) => {
        try {
            const response = await chatApi.sendMsg(message, room_id, participants, 'message');
            setMessages(prevMessages => [...prevMessages, response.data.message]);
            setChatList(prevList => {
                //    
                const updatedList = prevList.map(chat => {
                    if (Number(chat.room_id) === Number(room_id)) {
                        return {
                            ...chat,
                            last_message: response.data.message.content,
                            last_message_time: response.data.message.created_at,
                        };
                    }
                    return chat;
                });

                if (response.data.myRoomInfo) {
                    return [response.data.myRoomInfo, ...updatedList];
                }

                //      
                return updatedList;
            });
            return response;
        } catch (error) {
            console.error('  :', error);
            throw error;
        }
    };

    //   
    const fetchMessages = async () => {
        try {
            const response = await chatApi.msgList(room_id);
            setMessages(response.data.messages);
            setRoomInfo(response.data.roomInfo);
            setChatList(prevList =>
                prevList.map(chat => {
                    if (Number(chat.room_id) === Number(room_id)) {
                    return {
                        ...chat,
                        unread_count: 0
                    };
                    }
                    return chat;
                })
            );

        } catch (error) {
            console.error('   :', error);
        }
    };

    const formatTime = (dateString) => {
        if (!dateString) return '';
        const date = new Date(dateString);
        const now = new Date();
        const diff = now - date;
        const days = Math.floor(diff / (1000 * 60 * 60 * 24));

        if (days === 0) {
            return date.toLocaleTimeString('ko-KR', { hour: '2-digit', minute: '2-digit' });
        } else if (days === 1) {
            return '';
        } else if (days < 7) {
            return `${days} `;
        } else {
            return date.toLocaleDateString('ko-KR', { month: 'short', day: 'numeric' });
        }
    };

    const handleChatClick = async (friendId) => {
        try {
            const response = await chatApi.chatId(friendId);

            if (response.data.room_id) {
                sessionStorage.setItem('room_id', response.data.room_id);
                navigate('/chat');
            } else {
                alert('  .');
            }
        } catch (error) {
            console.error('  :', error);
            alert('   .  .');
        }
    };

    const createGroup = async (roomName, friendsId) => {
        try {
            const response = await chatApi.groupChat(roomName, friendsId);
            if(response.data){
                alert('   .');
                sessionStorage.setItem('room_id', response.data.room_id);
                setChatList(prevList => [response.data.roomInfo, ...prevList]);
                navigate('/chat');
            }
        } catch (error) { 
            console.error('   :', error);
        }
    }

    //  
    const handleChatLeave = async (room_id, room_type) => {
        if(window.confirm(' ?')){
            try {
                const response = await chatApi.leaveChat(room_id, room_type, participants, 'leave');
                if(response.data){
                    if(room_type == 'group') await chatApi.sendMsg(user.name+' .', room_id, participants, 'leave');
                    setChatList(prevList =>
                        prevList.filter(chat => Number(chat.room_id) !== Number(response.data.room_id))
                    );
                    alert('  .');
                    navigate('/chatList');
                }
            }catch (error) {
                console.error('  :', error);
            }
        }
    }

    //  
    const handleInviteFriends = async (room_id, room_type,friendsId, friendNames) => {
        try {
            const response = await chatApi.inviteFriends(room_id,room_type, friendsId, participants, 'invite');
            if(response.data){
                const namesStr = friendNames.join(', ');
                const response2 = await chatApi.sendMsg(user.name+' '+namesStr+' .', room_id, participants, 'invite');
                setMessages(prevMessages => [...prevMessages, response2.data.message]);
                setRoomInfo(prev => ({
                    ...prev,
                    user_names: response.data.user_names,
                    user_count: response.data.user_count
                }));
            }
        } catch (error) {
            console.error('  :', error);
            alert('  .');
        }
    };

    return {
        messages,
        setMessages,
        
        setRoomInfo,
        roomInfo,

        handleSendMessage,

        fetchMessages,

        isLoading,
        setIsLoading,

        formatTime,
        handleChatClick,

        createGroup,
        setParticipants,
        handleChatLeave,
        handleInviteFriends,
    };
};