// src/components/chat/CreateGroupChatModal.jsx
import React, { useEffect, useState } from 'react';
import { useFriendContext } from '../../../contexts/FriendContext';
import './CreateGroupChatModal.css';

const CreateGroupChatModal = ({ show, onHide, onCreate }) => {
    const [step, setStep] = useState(1);
    const [selectedFriends, setSelectedFriends] = useState([]);
    const { friends } = useFriendContext();
    const [groupName, setGroupName] = useState('');

    //   
    const handleFriendSelect = (friendId) => {
        setSelectedFriends(prev => {
            if (prev.includes(friendId)) {
                return prev.filter(id => id !== friendId);
            }
            return [...prev, friendId];
        });
    };

    //   
    const handleNext = () => {
        if (selectedFriends.length > 0) {
            setStep(2);
        }
    };

    //   
    const handleBack = () => {
        setStep(1);
    };

    //  
    const handleCreate = () => {
        if (groupName.trim() && selectedFriends.length > 0) {
            onCreate({
                name: groupName,
                participants: selectedFriends
            });
            setStep(1);
            setSelectedFriends([]);
            setGroupName('');
            onHide();
        }
    };

    if (!show) return null;

    return (
        <div className="modal-overlay">
            <div className="modal-content">
                <div className="modal-header">
                    <h2>{step === 1 ? ' ' : '  '}</h2>
                    <button className="close-btn" onClick={onHide}>&times;</button>
                </div>
                <div className="modal-body">
                    {step === 1 ? (
                        //   
                        <div className="friend-list">
                            {friends.map(friend => (
                                <div 
                                    key={friend.id}
                                    className={`friend-item ${selectedFriends.includes(friend.id) ? 'selected' : ''}`}
                                    onClick={() => handleFriendSelect(friend.id)}
                                >
                                    <div className="friend-info">
                                        <span className="friend-name">{friend.name}</span>
                                    </div>
                                </div>
                            ))}
                        </div>
                    ) : (
                        //    
                        <div className="group-name-input">
                            <label> </label>
                            <input
                                type="text"
                                value={groupName}
                                onChange={(e) => setGroupName(e.target.value)}
                                placeholder="  "
                            />
                        </div>
                    )}
                </div>
                <div className="modal-footer">
                    {step === 1 ? (
                        <>
                            <button className="btn cancel" onClick={onHide}></button>
                            <button 
                                className="btn next"
                                onClick={handleNext}
                                disabled={selectedFriends.length === 0}
                            >
                                
                            </button>
                        </>
                    ) : (
                        <>
                            <button className="btn back" onClick={handleBack}></button>
                            <button 
                                className="btn create"
                                onClick={handleCreate}
                                disabled={!groupName.trim()}
                            >
                                
                            </button>
                        </>
                    )}
                </div>
            </div>
        </div>
    );
};

export default CreateGroupChatModal;