import { useEffect, useRef, useState } from 'react';
import pusher from '../../api/pusher';
import MessageList from '../../components/chat/MessageList/MessageList';
import MessageInput from '../../components/chat/MessageInput/MessageInput';
import { useChat } from '../../services/chatService';
import { useAuth } from '../../contexts/AuthContext';
import InviteChatModal from '../../components/chat/InviteChatModal/InviteChatModal';
import '../../styles/ChatPage.css';

function ChatPage() {
    const { isAuthenticated, user } = useAuth();
    const [showInviteModal, setShowInviteModal] = useState(false);
    const { 
            roomInfo,
            messages,
            setMessages,
            handleSendMessage,
            fetchMessages,
            setParticipants,
            handleChatLeave,
            setRoomInfo,
            handleInviteFriends,
    } = useChat();
    const room_id = sessionStorage.getItem('room_id');
    const isSubscribed = useRef(false);
    const scrollRef = useRef(null);
    const scrollToBottom = () => {
        if (scrollRef.current) {
            scrollRef.current.scrollTo({
                top: scrollRef.current.scrollHeight,
                behavior: 'smooth'
            });
        }
    };

    useEffect(() => {
        if (!isAuthenticated) return;
        const channel = pusher.subscribePresence(`presence_chat.${room_id}`, {
            here: (users) => {
                setParticipants(users);
            },
            joining: (user) => {
                setParticipants(prev => [...prev, user]);
            },
            leaving: (user) => {
                setParticipants(prev => prev.filter(p => p.id !== user.id));
            }
        }).error((error) => {
            console.error('Presence channel error:', error);
        });

        return () => {
            if (channel) {
                pusher.unsubscribe(`presence_chat.${room_id}`);
            }
        };
    }, [isAuthenticated]);

    useEffect(() => {
        if (isAuthenticated && !isSubscribed.current) {
            fetchMessages();
            pusher.subscribe(`chat.${user.id}`, 'MessageSent', (e) => {
                if (Number(e.message.room_id) === Number(room_id)) {
                    setMessages(prevMessages => [...prevMessages, e.message]);
                }
            });
            pusher.subscribe(`read.${room_id}`, 'MessageRead', (e) => {
                //      
                if (e.userId !== user?.id) {
                    setMessages(prev =>
                        prev.map(msg => ({
                            ...msg,
                            unread_count: e.messages_count.find(m => m.message_id === msg.id)?.unread_count ?? 0
                        }))
                    );
                }
            });

            pusher.subscribe(`changeMember.${room_id}`, 'RoomMemberChangeEvent', (e) => {
                setRoomInfo(prev => ({
                    ...prev,
                    user_names: e.changeInfo.member,
                    user_count: e.changeInfo.count
                }));
            });
            isSubscribed.current = true;  //   
        }
        return () => {
            if (isSubscribed.current) {
                pusher.unsubscribe(`read.${room_id}`);
                isSubscribed.current = false;
            }
        };
    }, [isAuthenticated]);

    useEffect(() => {
        scrollToBottom();
    }, [messages]);

    const handleInvite = (selectedFriends, selectedFriendNames) => {
        handleInviteFriends(room_id, roomInfo.room_type, selectedFriends, selectedFriendNames);
    };

    return (
        <div
            ref={scrollRef}
            className="chatContainer"
        >
            <div className="chatHeader">
                <p className="chatInfo">
                    {roomInfo?.room_name 
                        ? `${roomInfo.room_name} ${roomInfo.user_count}`
                        : roomInfo?.user_names?.split(',').filter(item => item !== user?.name).join(',')}
                    {roomInfo?.room_type === 'group' && ` ( ${roomInfo?.user_names} ) `}
                </p>
                <div className='chatBtn'>
                    {roomInfo.room_type === 'group' && (
                        <button 
                            className="InviteBtn"
                            onClick={() => setShowInviteModal(true)}
                        >
                            
                        </button>
                    )}
                    <button className="leaveBtn" onClick={() => handleChatLeave(room_id, roomInfo.room_type)}></button>
                </div>
            </div>
            <div className="messageWrap">
                <MessageList messages={messages} currentUserId={user?.id} />
            </div>
            <MessageInput onSendMessage={handleSendMessage} />

            <InviteChatModal
                show={showInviteModal}
                onHide={() => setShowInviteModal(false)}
                onInvite={handleInvite}
                currentMembers={roomInfo.user_names}
            />
        </div>
    );
}

export default ChatPage; 