import '../../../styles/ChatPage.css';

const Message = ({ message, isMine, name, id, unread_count, action }) => {
  return (
    <>
      {action === 'message' ? (
        <div
          className={`message ${isMine ? 'myMessage' : 'otherMessage'}`}
          data-id={id}
        >
          {!isMine && <div className='senderLabel'>{name}</div>}
          <div
            className={`messageContent ${isMine ? 'myMessage2' : 'otherMessage2'}`}
          >
            <div className='messageText'>{message.content}</div>
          </div>
          <div
            className={`readCounting ${isMine ? 'myCounting' : 'otherCounting'}`}
          >
            {unread_count ? unread_count : ''}
            <div className='messageTime'>
              {new Date(message.created_at).toLocaleTimeString('ko-KR', {
                hour: 'numeric',
                minute: '2-digit',
              })}
            </div>
          </div>
        </div>
      ) : (
        <div className='actionMessage'>
          <p className='actionMessageText'>{message.content}</p>
        </div>
      )}
    </>
  );
};

export default Message;