import React, { useState, useEffect } from 'react';
import { useFriendContext } from '../../../contexts/FriendContext';
import './InviteChatModal.css';

const InviteChatModal = ({ show, onHide, onInvite, currentMembers }) => {
    const [selectedFriends, setSelectedFriends] = useState([]);
    const [memberArray, setMemberArray] = useState([]);
    const { friends } = useFriendContext();
    //   
    const handleFriendSelect = (friendId) => {
        setSelectedFriends(prev => {
            if (prev.includes(friendId)) {
                return prev.filter(id => id !== friendId);
            }
            return [...prev, friendId];
        });
    };

    // 
    const handleInvite = () => {
        if (selectedFriends.length > 0) {
            const selectedFriendNames = friends
                .filter(friend => selectedFriends.includes(friend.id))
                .map(friend => friend.name);
            
            onInvite(selectedFriends, selectedFriendNames);
            setSelectedFriends([]);
            onHide();
        }
    };

    useEffect(() => {
        if (currentMembers) {
            setMemberArray(currentMembers.split(','));
        }
    }, [currentMembers, show]);

    if (!show) return null;

    return (
        <div className="modal-overlay">
            <div className="modal-content">
                <div className="modal-header">
                    <h2> </h2>
                    <button className="close-btn" onClick={onHide}>&times;</button>
                </div>
                <div className="modal-body">
                    <div className="friend-list">
                        {friends.map(friend => (
                            <div 
                                key={friend.id}
                                className={`friend-item ${selectedFriends.includes(friend.id) ? 'selected' : ''}`}
                                onClick={() => !memberArray.includes(friend.name) && handleFriendSelect(friend.id)}
                                style={{ opacity: memberArray.includes(friend.name) ? 0.5 : 1 }}
                            >
                                <div className="friend-info">
                                    <span className="friend-name">{friend.name}</span>
                                    {memberArray.includes(friend.name) && (
                                        <span className="member-status">( )</span>
                                    )}
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
                <div className="modal-footer">
                    <button className="btn cancel" onClick={onHide}></button>
                    <button 
                        className="btn invite"
                        onClick={handleInvite}
                        disabled={selectedFriends.length === 0}
                    >
                        
                    </button>
                </div>
            </div>
        </div>
    );
};

export default InviteChatModal; 