import React, { createContext, useState, useContext, useEffect } from 'react';
import { useAuth } from './AuthContext';
import { friendApi } from '../api/friendApi';
import pusher from '../api/pusher';

const FriendContext = createContext();

export const FriendProvider = ({ children }) => {
    const { isAuthenticated, user } = useAuth();
    const [friends, setFriends] = useState([]);
    const [friendRequests, setFriendRequests] = useState([]);
    const [friendRequestCount, setFriendRequestCount] = useState(0);
    const [isLoading, setIsLoading] = useState(true);

    //   
    const fetchFriends = async () => {
        try {
            const response = await friendApi.getFriends()
            setFriends(response.data);
            setIsLoading(false);
        } catch (error) {
            console.error('   :', error);
            setIsLoading(false);
        }
    }

    //    
    const fetchFriendRequests = async () => {
        try {
            const response = await friendApi.getFriendRequests();
            setFriendRequests(response.data);
            // console.log(response.data);
            setFriendRequestCount(response.data.length);
        } catch (error) {
            console.error('    :', error);
        }
    };
    
    const friendEvent = (data) => {
        switch (data.status) {
            case 'accept':
            //   
            setFriends(prev => {
                const filtered = prev.filter(friend => friend.id !== data.userInfo.id);
                const updated = [...filtered, data.userInfo];
                updated.sort((a, b) => a.name.localeCompare(b.name));
                return updated;
            });
            break;

            case 'request':
            //   
            setFriendRequests(prev => {
                const filtered = prev.filter(friend => friend.id !== data.userInfo.id);
                const updated = [...filtered, data.userInfo];
                updated.sort((a, b) => a.name.localeCompare(b.name));
                return updated;
            });
            setFriendRequestCount(friendRequestCount + 1);
            break;

            case 'DELETE':
            setFriends(prev => prev.filter(friend => friend.id !== data.userInfo.id));
            break;

            case 'CANCEL':
            setFriendRequests(prev => prev.filter(friend => friend.id !== data.userInfo.id));
            setFriendRequestCount(friendRequestCount - 1);
            break;

            default:
            console.log('');
        }
    }
    
    useEffect(() => {
        if (isAuthenticated) {
            fetchFriends();
            fetchFriendRequests();
            pusher.subscribe(`Friend.${user.id}`, 'FriendEvent', (e) => {
                friendEvent(e);
            });
            return () => {
                pusher.unsubscribe(`Friend.${user.id}`);
            };

        }
    }, [isAuthenticated]);


    return (
        <FriendContext.Provider value={{ 
            isLoading,

            friendRequestCount,
            setFriendRequestCount,

            friends,
            setFriends,

            friendRequests,
            setFriendRequests,
        }}>
            {children}
        </FriendContext.Provider>
    );
};

export const useFriendContext = () => {
    const context = useContext(FriendContext);
    if (!context) {
        throw new Error('useFriendContext must be used within a FriendProvider');
    }
    return context;
};