import { useState } from 'react';
import { friendApi } from '../api/friendApi';
import { useFriendContext } from '../contexts/FriendContext';

export const useFriend = () => {
    const [searchResults, setSearchResults] = useState([]);
    const [isSearching, setIsSearching] = useState(false);
    const [activeTab, setActiveTab] = useState('tab1');
    const { 
        friendRequestCount,
        setFriendRequestCount,
        setFriends,
        setFriendRequests,
    } = useFriendContext();
    
    //  
    const handleSearch = async (query) => {
        if (!query.trim()) return;
        
        setIsSearching(true);
        try {
            const response = await friendApi.searchUsers(query);
            setSearchResults(response.data);
        } catch (error) {
            console.error('  :', error);
        } finally {
            setIsSearching(false);
        }
    };

    //  
    const handleAddFriend = async (userId) => {
        try {
            const response = await friendApi.addFriend(userId);
            setSearchResults(prev => 
                prev.map(user => 
                    user.id === userId 
                        ? {...user, status: 'pending'} 
                        : user
                )
            );
            // console.log(response.data);
            return response.data;
        } catch (error) {
            console.error('  :', error);
            throw error;
        }
    };

    //    
    const handleRemoveFriend = async (userId, type) => {
        const action_type = {
            DELETE: {
                confirm: '  ?',
                success: ' .'
            },
            CANCEL: {
                confirm: '  ?',
                success: ' .'
            }
        };

        try{
            if(window.confirm(action_type[type].confirm)){
                await friendApi.removeFriend(userId, type)
                alert(action_type[type].success);
                return true;
            }
        }catch(error){
            console.error('   :', error);
        }
    }

    //   
    const handleAcceptRequest = async (userId) => {
        try {
            const response = await friendApi.acceptRequest(userId)

            alert(response.data.message);
            setFriends(prev => {
                const filtered = prev.filter(friend => friend.id !== response.data.user.id);
                const updated = [...filtered, response.data.user];
                updated.sort((a, b) => a.name.localeCompare(b.name));
                return updated;
            });
            setFriendRequests(prev => prev.filter(friend => friend.id !== response.data.user.id));
            setFriendRequestCount(friendRequestCount - 1);
        } catch (error) {
            console.error('     :', error);
        }
    };

    //   
    const handleRejectRequest = async (userId) => {
        try {
            const response = await friendApi.rejectRequest(userId)

            alert(response.data.message);
            setFriendRequests(prev => prev.filter(friend => friend.id !== response.data.user.id));
            setFriendRequestCount(friendRequestCount - 1);
        } catch (error) {
            console.error('     :', error);
        }
    };

    return {
        searchResults,
        isSearching,
        activeTab,
        setSearchResults,
        setActiveTab,
        handleSearch,
        handleAddFriend,
        handleRemoveFriend,
        handleAcceptRequest,
        handleRejectRequest,
    };
};